.cant-find-Panel{
  padding: 10px;
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  background-color: white;
  border-style: solid;
  border-color: black;
  border-radius: 10px;
  z-index: 2;
  text-align: center;
  font-size: 20px;
}

.back-button{
  margin-top: 50px;
    text-align: center;
    height: 50px;
    margin:20px;
    min-width:100px;
    border-radius: 20px;
    background-color: rgb(0, 95, 0);
    color:white;
    font-size:20px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 49px;
}

.back-button:hover{
  background-color: green;
}